

.header-inner {
  max-width: $CONTENT-WIDTH;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-title {
  .header-heading a{
    padding-bottom: 5px;
    font-size: 2.2rem;
    font-weight: 600;
    color: $BASE2-COLOR;
  }
  .header-description {
    font-size: 1.0rem;
    color: $GRAY-COLOR;
  }
}
.header-nav {
  display: flex;
  ul {
    display: flex;
    li {
      
    }
  }
}
